<template>
  <div
    class="modal fade"
    ref="messageModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="alert alert-danger border-2 border-danger rounded-0" role="alert" v-if="!status">
        <i class="bi bi-exclamation-triangle me-2"></i>{{ message }}
      </div>
      <div class="alert alert-primary border-2 border-primary rounded-0" role="alert" v-else>
        <i class="bi bi-check-square me-2"></i>{{ message }}
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';

export default {
  data() {
    return {
      messageModal: '',
      message: '',
      status: false,
    };
  },
  methods: {
    openModal(e, data) {
      this.status = e;
      this.message = data;
      this.messageModal.show();
    },
  },
  mounted() {
    this.messageModal = new Modal(this.$refs.messageModal);
  },
};
</script>
